import React, {Suspense} from "react";
import RetirementMain from "../Home/images/Retirement/1.png";
import { withTranslation } from "react-i18next";
import {Helmet} from 'react-helmet';

const RetirementSection = React.lazy(()=>import("./sections/retirementPlansection"));

export class Retirement extends React.Component {
  render() {
    const { t } = this.props;
    return (
      <section>
        <Helmet>
                <meta charSet="utf-8" />
                <link rel="canonical" href={window.location.href} />
            </Helmet>
        <div id="Retirement-Banner">
          <div className="container container-wrapper">
            <div className="row">
              <div className="col-lg-7 col-md-6 col-12 reitred-box">
                <h2>{t("RetirementPlan:RP1")}</h2>
                <p>{t("RetirementPlan:RP2")}</p>
                <a
                  href="https://app.nivesh.com/home/index"
                  className="btn btn-primary"
                  target="_blank"
                >
                  {t('CTAFooter:InvestNow1')}
                </a>
              </div>

              <div className="col-lg-5 col-md-6 col-12">
                <img src={RetirementMain} alt="" className="img-fluid" />
              </div>
            </div>
          </div>
        </div>

        <div id="arncode-section">
          <div className="container container-wrapper">
            <div className="row ">
              <h2>{t("RetirementPlan:RP3")}</h2>
              <ul>
                <li>{t("RetirementPlan:RP4")}</li>
                <li>{t("RetirementPlan:RP5")}</li>

                <li>
                  Most of the schemes levy an exit load on redemptions before a
                  particular age.
                </li>
                <li>{t("RetirementPlan:RP6")}</li>
              </ul>
            </div>
          </div>
        </div>

        <Suspense fallback={<div>Loading....</div>}><RetirementSection /></Suspense>

      </section>
    );
  }
}

export default withTranslation()(Retirement);
